"use client"

import { useState } from "react"
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
} from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"

interface CreateCampaignModalProps {
  open: boolean
  onOpenChange: (open: boolean) => void
  onViewCampaigns?: () => void
  onSubmit?: (data: CampaignFormData) => void
}

export interface CampaignFormData {
  name: string
  organization: string
  businessFunction: string
  numberOfTargets: number
}

export function CreateCampaignModal({
  open,
  onOpenChange,
  onViewCampaigns,
  onSubmit,
}: CreateCampaignModalProps) {
  const [formData, setFormData] = useState<CampaignFormData>({
    name: "",
    organization: "",
    businessFunction: "",
    numberOfTargets: 4,
  })

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    if (onSubmit) {
      onSubmit(formData)
    }
    // Reset form
    setFormData({
      name: "",
      organization: "",
      businessFunction: "",
      numberOfTargets: 4,
    })
    onOpenChange(false)
  }

  const handleCancel = () => {
    setFormData({
      name: "",
      organization: "",
      businessFunction: "",
      numberOfTargets: 4,
    })
    onOpenChange(false)
  }

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-[600px] bg-card border-border">
        <DialogHeader className="space-y-3">
          <div className="flex items-start justify-between">
            <div className="flex-1">
              <DialogTitle className="text-2xl font-bold text-foreground">
                Create New Campaign
              </DialogTitle>
              <DialogDescription className="text-muted-foreground mt-2">
                Configure your phishing simulation campaign parameters.
              </DialogDescription>
            </div>
            {onViewCampaigns && (
              <Button
                variant="outline"
                onClick={onViewCampaigns}
                className="border-border bg-transparent"
              >
                View Campaigns
              </Button>
            )}
          </div>
        </DialogHeader>

        <form onSubmit={handleSubmit} className="space-y-6 mt-6">
          {/* Campaign Name */}
          <div className="space-y-2">
            <Label htmlFor="campaign-name" className="text-sm font-medium text-foreground">
              Campaign Name
            </Label>
            <Input
              id="campaign-name"
              type="text"
              value={formData.name}
              onChange={(e) => setFormData({ ...formData, name: e.target.value })}
              placeholder="Q1 2025 Security Training"
              className="bg-secondary border-border text-foreground placeholder:text-muted-foreground"
              required
            />
          </div>

          {/* Organization */}
          <div className="space-y-2">
            <Label htmlFor="organization" className="text-sm font-medium text-foreground">
              Organization
            </Label>
            <Select
              value={formData.organization}
              onValueChange={(value) =>
                setFormData({ ...formData, organization: value })
              }
              required
            >
              <SelectTrigger
                id="organization"
                className="w-full bg-secondary border-border text-foreground"
              >
                <SelectValue placeholder="Select organization" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="acme-corp">Acme Corporation</SelectItem>
                <SelectItem value="tech-startup">Tech Startup Inc.</SelectItem>
                <SelectItem value="finance-group">Finance Group Ltd.</SelectItem>
                <SelectItem value="healthcare-sys">Healthcare Systems</SelectItem>
              </SelectContent>
            </Select>
          </div>

          {/* Business Function */}
          <div className="space-y-2">
            <Label htmlFor="business-function" className="text-sm font-medium text-foreground">
              Business Function
            </Label>
            <Select
              value={formData.businessFunction}
              onValueChange={(value) =>
                setFormData({ ...formData, businessFunction: value })
              }
              required
            >
              <SelectTrigger
                id="business-function"
                className="w-full bg-secondary border-border text-foreground"
              >
                <SelectValue placeholder="Select function" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="finance">Finance</SelectItem>
                <SelectItem value="hr">Human Resources</SelectItem>
                <SelectItem value="it">IT Department</SelectItem>
                <SelectItem value="executive">Executive</SelectItem>
                <SelectItem value="operations">Operations</SelectItem>
                <SelectItem value="sales">Sales</SelectItem>
                <SelectItem value="marketing">Marketing</SelectItem>
              </SelectContent>
            </Select>
          </div>

          {/* Number of Targets */}
          <div className="space-y-2">
            <Label htmlFor="number-of-targets" className="text-sm font-medium text-foreground">
              Number of Targets
            </Label>
            <Input
              id="number-of-targets"
              type="number"
              min="1"
              value={formData.numberOfTargets}
              onChange={(e) =>
                setFormData({
                  ...formData,
                  numberOfTargets: parseInt(e.target.value) || 0,
                })
              }
              className="bg-secondary border-border text-foreground"
              required
            />
            <p className="text-xs text-muted-foreground">
              Number of employees to include in this campaign
            </p>
          </div>

          {/* Action Buttons */}
          <div className="flex items-center justify-end gap-3 pt-4">
            <Button
              type="button"
              variant="outline"
              onClick={handleCancel}
              className="border-border bg-transparent"
            >
              Cancel
            </Button>
            <Button
              type="submit"
              className="bg-primary hover:bg-primary/90 text-primary-foreground shadow-sm"
            >
              Launch Campaign
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  )
}

